.main {
  display: flex;
  justify-content: center;
  padding-top: 88px;
}
@media screen and (min-width: 901px) {
  .header-nav {
    display: none;
  }
}
@media screen and (max-width: 900px) {
  .ant-layout-sider {
    display: none;
  }
  .ant-breadcrumb {
    display: none;
  }
  .header {
    padding: 0 24px;
  }
}
